import PropTypes from 'prop-types';
import React, { useState } from 'react';
import {
  Navbar,
  NavbarBrand,
  NavbarToggler,
  Nav,
  NavItem,
  NavLink,
  Collapse
} from 'reactstrap';

const Header = ({siteTitle, showTitle}) => {

  const [isOpen, setOpen] = useState(false);

  return (
    <header>
      <Navbar expand="md" className="navbar-dark bg-transparent">
        { showTitle && <NavbarBrand href="/"><h1 className="title--header brand">{siteTitle}</h1></NavbarBrand> }
        <NavbarToggler className="link light" onClick={() => setOpen(!isOpen)} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink className="link light" href="/about/">About</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </header>
  );
};

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
