import React from 'react'
import { Link } from 'gatsby'

const CategoryIcon = ({ path, icon, name }) => {
  return (
    <Link className="link" to={path}>
      <span
        title={name}
        className="tag-icons pr-2"
        dangerouslySetInnerHTML={{ __html: icon.map(i => `&#x${i.replace('U+', '')}`).join('') }}>
      </span>
    </Link>
  )
}

export default CategoryIcon