import React from 'react';
import { Link } from 'gatsby'
import CategoryIcon from './category-icon'

const ListingSummary = ({title, description, url, icons}) => (
  <div className="row listing">
    <div className="col-md">
      <div className="card mb-3">
        <div className="card-body">
          <h5 className="card-title title--header dark small"><Link className="link" to={url}>{ title }</Link></h5>
          <p className="card-text">{ description }</p>
          <p>
            {
              icons && icons.map((ic, idx) => (
                <CategoryIcon icon={ic.icon} name={ic.name} path={ic.path} key={idx}/>
              ))
            }
          </p>
        </div>
      </div>
    </div>
  </div>
);

export default ListingSummary;