import React, { useEffect, useRef, useState } from 'react'
import * as algoliasearch from 'algoliasearch'
import ListingSummary from './listing-summary'
import { graphql, StaticQuery } from 'gatsby'
import CategoryIcon from './category-icon'
import SearchResult from './search-result'

const Search = ({ query, onResults }) => {

  const [index, setIndex] = useState(null);
  const [content, setContent] = useState({});
  const [page, setPage] = useState(0);
  const searchContainerRef = useRef(null);

  useEffect(() => {
    const client = algoliasearch('V2FZ8PQPU1', '53dfa38126e32c85b534b72c3830168d');
    setIndex(client.initIndex('expatty'));
  }, []);

  useEffect(() => {
    if (!index || !query) {
      return;
    }
    index.search({ query, page, hitsPerPage: 10 }, (err, content) => {
      if (err) {
        console.error(err);
      } else {
        searchContainerRef.current.scrollTop = 0;
        setContent(content);
        onResults(content.nbHits);
      }
    });
  }, [query, page]);

  return (
    <StaticQuery
  query={graphql`
      {
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
              edges {
                  node {
                      frontmatter {
                          tag
                          name
                          icon
                      }
                      fields {
                          slug
                      }
                  }
              }
          }
        }
    `}
  render={
    data => {
      return (
        <>
          <div className="search" ref={ searchContainerRef }>
            {
              content.hits && content.hits.map((h, idx) => {
                const listingIcons = h.tags
                  .map(tg => data.categories.edges.map(edge => edge.node).find(catNode => catNode.frontmatter.tag === tg))
                  .filter(catNode => catNode)
                  .map(catNode => ({
                    icon: catNode.frontmatter.icon,
                    name: catNode.frontmatter.name,
                    path: catNode.fields.slug
                  }));
                return (
                    <SearchResult key={ idx } description={ h.description } icons={ listingIcons } url={ h.url } title={ h.title }/>
                );
              })
            }
          </div>
            {
              (content.nbPages && content.nbPages > 1) ? <div className="search-pagination">
                <nav className="mt-5" aria-label="Page navigation example">
                  <ul className="pagination justify-content-center">
                    {page > 0 && <li className="page-item">
                      <button className="page-link accent-colour" aria-label="Previous" onClick={() => setPage(Math.max(0, page - 1))}>
                        <span aria-hidden="true">&laquo;</span>
                        <span className="sr-only">Previous</span>
                      </button>
                    </li>}
                    {
                      new Array(content.nbPages)
                        .fill(null)
                        .map((link, idx) => (
                          <li key={idx} className="page-item">
                            <button disabled={page === idx} className={`page-link${page === idx ? ' bold primary-colour' : ' accent-colour'}`} onClick={() => setPage(idx)}>{ idx + 1 }</button>
                          </li>)
                        )
                    }
                    { page < content.nbPages - 1 && <li className="page-item">
                      <button className="page-link accent-colour" aria-label="Next" onClick={() => setPage(Math.min(page + 1, content.nbPages - 1))}>
                        <span aria-hidden="true">&raquo;</span>
                        <span className="sr-only">Next</span>
                      </button>
                    </li>}
                  </ul>
                </nav>
              </div> : undefined
            }
        </>
    )
    }}
  />
  )
};

export default Search;